@import "variables";
@import "mixins";

html,
body {
    margin: 0;
    padding: 0;
    @media (max-width:500px) {
        overflow-x: hidden;
    }
    &.hid {
        overflow: hidden;
    }
}
* {
     box-sizing: border-box;
}
.flex {
    display: flex;
}
body {
    font-family: Rubik;
    font-style: normal;
    font-weight: normal;
    font-size: 17px;
    line-height: 150%;
    
}
.wrapper {
    max-width: 1215px;
    padding: 0 10px;
    margin: 0 auto;
}
a {
    text-decoration: none;
    color: #000;
    transition: $tr;
    &:hover {
        color: $red;
    }
}
.h1 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 900;
    font-size: 170px;
    line-height: 207px;
    
    padding: 0;
    margin: 0 0 100px 0;
    
    overflow: hidden;
    &::first-letter {
        color: $red;
    }
    span {
        display: block;
        margin-bottom: -50px;
    }
    @media (max-width:1125px) {
        font-size: 120px;
        line-height: 146px;
        margin: 0 0 70px 0;
        span {
            display: block;
            margin-bottom: -30px;
        }
    }
    @media (max-width:960px) {
        font-size: 100px;
        line-height: 122px;
        margin: 0 0 40px 0;
        span {
            display: block;
            margin-bottom: -27px;
        }
    }
    @media (max-width:700px) {
        font-size: 58px;
        line-height: 71px;
        margin: 0 0 20px 0;
        span {
            display: block;
            margin-bottom: -20px;
        }
    }
    @media (max-width:650px) {
        font-size: 36px;
        line-height: 44px;
        margin: 0 0 20px 0;
        span {
            display: block;
            margin-bottom: -10px;
        }
    }
    
}
.h2 {
    font-family: Rubik;
    font-style: normal;
    font-weight: 500;
    font-size: 32px;
    line-height: 150%;
    &-page {
        font-family: Montserrat;
        font-style: normal;
        font-weight: 900;
        font-size: 54px;
        line-height: 66px;
        
        text-align: center;
        @media (max-width:800px) {
            font-size: 42px;
            line-height: 51px;
        }
        @media (max-width:600px) {
            font-size: 32px;
            line-height: 39px;
        }
        @media (max-width:450px) {
            font-size: 24px;
            line-height: 29px;
        }
        
        
    }
    @media (max-width:1000px) {
        font-size: 32px;
        line-height: 150%;
    }
    @media (max-width:800px) {
        font-size: 32px;
        line-height: 150%;
    }
    @media (max-width:600px) {
        font-size: 24px;
        line-height: 150%;
    }
    @media (max-width:400px) {
        font-size: 20px;
    }
    
}
p {
    font-family: Rubik;
    font-style: normal;
    font-weight: normal;
    font-size: 17px;
    line-height: 150%;
    @media (max-width:450px) {
        font-size: 15px;
    }
}
.label {
    display: flex;
    align-items: center;
    cursor: pointer;
    
    margin-bottom: 15px;
    span {
        display: inline-block;
    }
    &-span {
        width: 28px;
        height: 28px;
        min-width: 28px;
        min-height: 28px;
        
        border: 2px solid #D62D30;
        box-sizing: border-box;
        border-radius: 4px;
        margin-right: 12px;
    }
    input {
        display: none;
        & + span {
            
        }
        &:checked {
            & + span {
                background: $red url(../images/check.png) no-repeat center;
                
            }
        }
    }
    &-color {
        .label {
            &-span {
                width: 58px;
                height: 58px;
                min-width: 58px;
                min-height: 58px;
                border-radius: 50%;
                display: inline-flex;
                border-color: rgba(0,0,0,0);
                cursor: pointer;
                
                align-items: center;
                justify-content: center;
            }   
        }
        input {
            display: none;
            & + span {

            }
            &:checked {
                & + span {
                    border-color: inherit;

                }
            }
        }
    }
}
.block {
    &-bg {
        background-color: #F9F9F9;
        
        &.catalog__item {
            background-image: url(../images/item-bg.png);
            background-repeat: no-repeat;
            background-position: center;
        }
        
        border-radius: 10px;
        &:not(.block-bgNo):hover {
            background: #FFFFFF;
            box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.1);
        }

        &No {

        }
    }
    &-bottom {
        margin-bottom: 30px;
    }
}
.fixed {
    left: 0;
    background: #fff;
    right: 0;
    z-index: 50;
    display: block!important;
    &.top {
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        background: #fff;
        z-index: 50;
        box-shadow: 0px 4px 20px rgba(113, 113, 113, 0.5);
        
    }
    .btn-m {
        display: none;
    }
    @media (max-width:960px) {
        position: fixed;
        left: 0;
        right: 0;
        bottom: 0;
        top: 50px;
        background: #000;
        z-index: 50;
        display: none!important;
        &.active {
            display: block!important;
        }
        .wrapper,
        .menu {
            flex-direction: column;
            text-align: center;
        }
        .menu {
            li+li {
                margin-left: 0;
            }
            a {
                margin: 10px 0;
                display: block;
                color: #fff;
                font-size: 24px;
                line-height: 29px;
            }
        }
        
        .btn {
            margin:15px 0 0 0!important;
            display: block!important;
            background: $red;
            color: #fff;
            &-m {
                display: block;
            }
        }
        
    }
}
.fancybox-slide {
    .fixed {
        display: inline-block!important;
    }
}

.play {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    transition: background ease 0.4s;
    display: block;
    &:hover {
        background: rgba(0,0,0,0.4);
        span {
            &:not(.play-btn) {
                animation: sh 1.6s infinite ease-in-out;
                width: 156px;
                height: 156px;
            }
        }
    }
    span {
        position: absolute;
        z-index: 1;
        display: block;
        width: 96px;
        height: 96px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border: 2px solid #fff;
        border-radius: 50%;
        opacity: 0;
        transition: all ease 0.4s;
    }
    @keyframes sh {
        from {
            width: 96px;
            height: 96px;
        }
        50% {
            opacity: 1;
        }
        to {
            width: 156px;
            height: 156px;
        }
    }
    &-btn {
        content: '';
        background: url(../images/news/play.png) no-repeat center #D62D30;
        border: 10px solid #fff!important;
        border-radius: 50%;
        opacity: 1!important;
        z-index: 2;
    }
    
}
