.header {
    .flex {
        align-items: center;
        justify-content: space-between;
    }
    .btn {
        margin-left: 20px;
        @media (max-width:700px) {
            &:not(.btn__menu) {
                display: none;
            }
        }
    }
    .wrapper {
        padding-top: 29px;
        padding-bottom: 29px;
        @media (max-width:780px) {
            padding-top: 17px;
            padding-bottom: 17px;
        }
    }
    hr {
        height: 1px;
        background: #EEEEEE;
        margin: 0 15px;
        border: none;
        outline: none;
        @media (max-width:780px) {
            display: none;
        }
    }
    &-top {
        @media (max-width:960px) {
            & > div.flex {
                flex: 1 1 100%;
            }
            margin-bottom: 0!important;
        }
    }
}
.logo {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
    img {
        margin-right: 37px;
        @media (max-width:400px) {
            width: 100px;
        }
    }
    span {
        @media (max-width:960px) {
            display: none;
        }
    }
}
.lang {
    min-width: 60px;
    margin-right: 20px;
    a {
        color: $grey;
    }
} 