.btn {
    font-family: 'Rubik', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 17px;
    line-height: 20px;
    text-align: center;
    
    padding: 12px 23px;
    color: $red;
    
    border: 2px solid $red;
    border-radius: 4px;
    display: inline-block;
    
    &:hover {
        box-shadow: inset 0 0 0px 2px $red;
    }
    &:active {
        border-color: #871A1C;
        background: #871A1C;
        box-shadow: inset 0 0 0px 2px #871A1C;
        color: #fff;
        span {
            color: #fff;
        }
    }
    span {
        display: flex;
        align-items: center;
        justify-content: center;
        
        color: #000;
        img {
            margin-right: 7px;
        }
    }
    &P {
        background: $red;
        color: #fff;
        &:hover {
            color: #fff;
            background: #E57C7E;
            border-color: #E57C7E;
            box-shadow: inset 0 0 0px 2px #E57C7E;
        }
        &:active {
            color: #fff;
            background: #871A1C;
            border-color: #871A1C;
            box-shadow: inset 0 0 0px 2px #871A1C;
        }
    }
    &W {
        border-color: #fff;
        color: #fff;
        &:hover {
            color: $red;
            background: #fff;
            border-color: #fff;
            box-shadow: inset 0 0 0px 2px #fff;
        }
        &:active {
            color: $red;
            background: #fff;
            border-color: #fff;
            box-shadow: inset 0 0 0px 2px #fff;
        }
    }
    &__menu {
        display: none;
        justify-content: center;
        align-items: center;
        padding: 10.5px 12px;
        @media (max-width:960px) {
            display: flex;
        }
        span {
            width: 18px;
            margin: 10px 0;
            height: 2px;
            background: $red;
            border-radius: 5px;
            position: relative;
            &:after,
            &:before {
                content: '';
                display: block;
                position: absolute;
                left: 0;
                right: 0;
                height: 2px;
                background: $red;
                border-radius: 5px;
            }
            &:after {
                top: -7px;   
            }
            &:before {
                bottom: -7px;
            }
        }
        &.active {
            background: $red;
            span {
                &:after,
                &:before {
                    background: #fff;
                    top: 0px;  
                    bottom: 0px;
                }
                &:after {
                    transform: rotate(-45deg);
                }
                &:before {
                    transform: rotate(45deg);
                }
            }
        }
    }
    &__close {
        width: 50px;
        height: 50px;
        background: $red;
        display: none;
        justify-content: center;
        align-items: center;
        
        position: absolute;
        right: 0;
        top: 0;
        img {
            width: 20px;
        }
        @media (max-width:960px) {
            display: flex;
        }
    }
}