
.catalog {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 100px;
    @media (max-width:500px) {
        margin-bottom: 15px;
    }
    &__item {
        width: 30%;
        margin: 0 1.5% 30px 1.5%;
        padding: 30px 10px;
        text-align: center;
        position: relative;
        @media (max-width:1000px) {
            width: 47%;   
        }
        @media (max-width:479px) {
            width: 97%;   
        }
        
        &-slider {
            & > .slick-arrow {
                display: none!important;
            }
            & > .slick-list {
                margin: 0 0px!important;
                & > .slick-track > .slick-slide > a {
                    display: block;
                    text-align: center;
                    padding: 0 20px;
                }
            }
        }
        .slick-list {
            margin: 0 20px;
        }
        img {
            display: inline-block!important;
            width: 100%;
            max-width: 268px;
        }
        &-img {
            background:#F9F9F9;
        }
        &:hover {
            .catalog__item-img {
                background: #fff;
            }
        }
        &-head {
            font-family: Rubik;
            font-style: normal;
            font-weight: normal;
            font-size: 24px;
            line-height: 150%;
            
            margin-bottom: 10px;
            
            height: 36px;
            
            @media (max-width:600px) {
                font-size: 15px;
            }
            
            &.new {
                color: $red;
            }
        }
        &-name {
            font-family: Rubik;
            font-style: normal;
            font-weight: 500;
            font-size: 20px;
            line-height: 24px;
            
            padding-top: 10px;
            @media (max-width:600px) {
                font-size: 15px;
            }
        }
//        &-img {
//            display: flex;
//            justify-content: center;
//            img {
//                max-width: 60px;
//            }
//            a {
//                margin: 15px 5px;
//            }
//        }
        
        .slick-dots {
            li {
                height: 27px;
                width: 27px;
                button {
                    border-color: rgba(0,0,0,0);
                    &:before {
                        width: 20px;
                        height: 20px;
                        background-clip: inherit;
                    }
                }
                &.slick-active {
                    button {
                        border-color: rgba(0,0,0,0);
                    }
                }
            }
        }
        &-dots {
            display: flex;
            justify-content: center;
            align-items: center;
            color: #000;
            &-a {
                margin: 15px 7px;
                width: 20px;
                height: 20px;
                border-radius: 50%;
                display: block;
                background-color: inherit;
                background-color: none;
                img {
                    width: 100%;
                }
            }
        }
    }
}

.item {
    justify-content: space-between;
    margin-bottom: 100px;
    flex-wrap: wrap;
    @media (max-width:600px) {
         margin-bottom: 0px;  
    }
    &__img {
        width: 48%;
        margin-bottom: 40px;
        border: 1px solid #DCDCDC;
        box-sizing: border-box;
        border-radius: 10px;
        padding: 60px 45px 20px 40px;
        @media (max-width:500px) {
            margin-bottom: 15px;
        }
        @media (max-width:820px) {
            width: 100%;
        }
        
        .slick-initialized .slick-slide a {
            height: 100%;
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .slick-list {
            margin: 0 20px;
        }
        &Min {
            justify-content: center;
            a {
                margin: 15px 5px;
                display: flex;
                min-width: 80px;
                min-height: 80px;
                width: 80px;
                height: 80px;
                align-items: center;
                justify-content: center;
                &.active {
                    border: 1px solid $red;
                    border-radius: 5px;
                }
            }
            img {
                max-width: 60px;
            }
        }
    } 
    &__info {
        font-family: Rubik;
        font-style: normal;
        font-weight: normal;
        font-size: 24px;
        line-height: 150%;
        margin-bottom: 40px;
        width: 48%;
        @media (max-width:500px) {
                margin-bottom: 15px;
            }
        @media (max-width:820px) {
            width: 100%;
        }
        p {
            margin-top: 0;
            padding-top: 0;
            
            margin-bottom: 40px;
            @media (max-width:500px) {
                margin-bottom: 15px;
            }
        }
        .page__catalog-sort-color {
            padding-left: 0;
            margin-bottom: 40px;
            border: none;
            @media (max-width:500px) {
                margin-bottom: 15px;
            }
        }
        .btn {
            margin-right: 20px;
            @media (max-width:820px) {
                flex: 1 1 100%;   
            }
            @media (max-width:600px) {
                margin: 0 0 15px 0 !important;   
            }
        }
        @media (max-width:600px) {
            & > .flex {
                flex-wrap: wrap;
            }
        }
        &-f {
            @media (max-width:820px) {
                display: flex;
                justify-content: space-between;
                width: 100%;
                .page__catalog-sort-color {
                    flex: 0 0 auto;
                }
            }
            @media (max-width:700px) {
                flex-direction: column;
                align-items: center;
                justify-content: center;
            }
        }
    }
    &-info {
        justify-content: space-between;
        @media (max-width:830px) {
            flex-direction: column;
        }
        &-block {
            width: 48%;
            @media (max-width:830px) {
                width: 100%;
            }
        }
        h2 {
            font-family: Montserrat;
            font-style: normal;
            font-weight: 900;
            font-size: 32px;
            line-height: 39px;
        }
        &-slider {
            .slick-list {
                margin: 0px;
            }
            .slick-arrow {
                display: none!important;
            }
        }
    }
}
