.block {
    margin-bottom: 50px;
}
.where {
    &-item {
        justify-content: space-between;
        min-height: 200px;
        align-items: center;
        background: $bg;
        margin-bottom: 30px;
        border-radius: 10px;
        
        transition: $tr;
        &:hover {
            background: #FFFFFF;
            box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.1);
        }
        @media (max-width:600px) {
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
            padding: 30px;
        }
    }
    a {
        font-family: Montserrat;
        font-style: normal;
        font-weight: 900;
        font-size: 24px;
        line-height: 150%;
        color: $red;
        &:hover {
            color: #000;
        }
    }
    &-text {
        flex-direction: column; 
        width: 50%;
        word-break: break-all;
        @media (max-width:820px) {
            width: 60%;
        }
        @media (max-width:600px) {
            width: 100%;
        }
    }
    &-img {
        align-items: center;
        justify-content: center;
        width: 50%;
        @media (max-width:820px) {
            width: 40%;
        }
        @media (max-width:600px) {
            justify-content: flex-start;
            margin-bottom: 25px;
            width: 100%;
        }
    }
}

.social {
    justify-content: space-between;
}

.page {
    &__image {
        
        position: relative;
        text-align: center;
        img {
            width: 100%;
            display: block;
            position: relative;
            z-index: 1;
        }
        span {
            position: relative;
            display: block;
            margin: 0 15px;
            max-width: 1860px;
            overflow: hidden;
            border-radius: 10px;
            @media (max-width:500px) {
                margin: 0px;
                border-radius: 0px;
            }
            @media (min-width:499px) {
                &:after {
                    content: '';
                    position: absolute;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    min-width: 450px;
                    background: url(../images/bg/img_page.png) no-repeat top right;
                    z-index: 2;
                    background-size: contain;
                }
            }
        }
        &-index {
            overflow: hidden;
            span {
                &:after {
                    display: none!important;
                }
                @media (max-width:1250px) {
                    margin: 0;
                    border-radius: 0;
                }
                img {
                    width: calc( 100% + 300px);
                    margin-left: -100px;
                }
                
            }
            .wrapper {
                padding: 20px!important;
                align-items: flex-start!important;
                @media (max-width:600px) {
                    padding: 0px!important;
                }
            }
            .page__image-text {
                display: block!important;
            }
            

            h1 {
                font-family: Montserrat;
                font-style: normal;
                font-weight: 900;
                font-size: 72px;
                line-height: 88px;
                @media (max-width:1630px) {
                    font-size: 54px;
                    line-height: 66px;
                }
                @media (max-width:1220px) {
                    font-size: 42px;
                    line-height: 51px;
                }
                @media (max-width:1000px) {
                    font-size: 36px;
                    line-height: 39px;
                }
                @media (max-width:800px) {
                    font-size: 28px;
                    line-height: 29px;
                }
                
            }
            &_right,
            &_left {
                width: 50%;
                font-family: Rubik;
                font-style: normal;
                font-weight: normal;
                font-size: 32px;
                line-height: 150%;
                @media (max-width:1090px) {
                    font-size: 24px;
                    line-height: 150%;
                }
                @media (max-width:900px) {
                    font-size: 19px;
                       line-height: 100%;
                }
                
            }
            &_left {
                @media (max-width:690px) {
                    display: none;
                }
            }
            &_right {
                position: relative;
                    height: 100%;
                @media (max-width:690px) {
                    width: 100%;
                    
                }
                &:after {
                    content: '';
                    display: block;
                    width: 967px;
                    height: 100%;
                    position: absolute;
                    bottom: -20px;
                    left: 0;
                    background: url(../images/bg/chil.png) no-repeat left bottom;
                    background-size: contain;
                    
                    @media (max-width:690px) {
                        width: 100%;
                        background: url(../images/bg/chil.png) no-repeat center bottom;
                        background-size: contain;
                    }
                    @media (max-width:600px) {
                        bottom: 0;   
                    }
                }
            }
            .btn {
                display: inline-block;
            }
        }
        &-text {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            @media (max-width:600px) {
                display: none;
            }
        }
        .wrapper {
            padding: 20px 20px 100px 20px;
            display: flex;
            align-items: flex-end;
//            align-items: center;
            justify-content: flex-start;
            position: relative;
            z-index: 2;
            text-align: left;
            height: 100%;
            
            
            font-family: Montserrat;
            font-style: normal;
            font-weight: 900;
            font-size: 54px;
            line-height: 66px;
            
            color: #fff;
            @media (max-width:1100px) {
                align-items: center;
                padding: 20px;
                
            }
            @media (max-width:768px) {
                font-size: 32px;
                line-height: 39px;
            }
            
        }
    }
    &__about {
        justify-content: space-between;
        margin-bottom: 50px;
        @media (max-width:500px) {
                margin-bottom: 15px;
            }
        @media (max-width:760px) {
            margin-bottom: 20px;   
        }
        &2 {
            flex-direction: column;
            @media (min-width:900px) {
                flex-direction: row;
                & div {
                    width: 44%;
                }
            }
        }
        &3 {
            font-family: Montserrat;
            font-style: normal;
            font-weight: 900;
            font-size: 32px;
            line-height: 150%;
            color: $red;
            @media (max-width:1000px) {
                font-size: 28px;
            }
            @media (max-width:760px) {
                flex-direction: column;
                br {
                    display: none;
                }
            }
            @media (max-width:600px) {
                font-size: 24px;
                
            }
        }
    }
    &__partner {
        background: #F9F9F9;
        border-radius: 10px;
        margin: 0 10px;
        padding: 90px 0;
        @media (max-width:700px) {
            padding: 10px 0;
        }
        &-item {
            display: flex!important;
            justify-content: center;
            align-items: center;
            text-align: center;
            
            
            span {
                display: flex;
                align-items: center;
                text-align: center;
                justify-content: center;
                height: 100%;
                background: #fff;
                border-radius: 10px;
                width: 100%;
                margin: 0 10px;
            }
            * {
                outline: none;
            }
        }
        &-slider {
            @media (max-width:1100px) {
                .slick-list {
                    margin: 0 40px;
                }
                .slick-prev, .slick-next {
                    top: 30%;
                }
            }
            
        }
    }
    &__sert {
        &-slider {
            img {
                width: 100%;
                height: auto;
            }
            @media (max-width:1000px) {
                 .slick-list {
                    margin: 0;
                }  
            }
        }
    }
    
    &__catalog {
        &-sort {
            font-family: Rubik;
            font-style: normal;
            font-weight: normal;
            font-size: 17px;
            
            margin-bottom: 30px;
            @media (max-width:700px) {
                flex-direction: column;
                margin-bottom: 0;
            }
            &-block {
//                width: 100%;
                flex-wrap: wrap;
                flex: 1 1 auto;
                align-items: center;
                @media (max-width:700px) {
                    border-top: 1px solid #DCDCDC;
                    .page__catalog-sort-block {
                        border: none;
                    }
                }
            }
            &-head {
                font-family: Montserrat;
                font-style: normal;
                font-weight: 900;
                font-size: 24px;
                line-height: 150%;
                &:not(.color) {
                    margin-bottom: 30px;
                }
                img {
                    display: none;
                }
                @media (max-width:700px) {
                    padding-top: 25px;   
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    width: 100%;
                    margin-bottom: 30px;
                    img {
                        display: block;
                    }
                    & + .page__catalog-sort-block {
                        display: none;
                    }
                    &.active {
                        img {
                            display: block;
                            transform: rotate(-180deg)
                        }
                        & + .page__catalog-sort-block {
                            display: flex;
                        }
                    }
                }
            }
            .label {
                width: 33%;
                @media (max-width:1000px) {
                    width: 50%;
                }
                @media (max-width:500px) {
                    width: 100%;
                }
            }
            &-color {
                flex: 1 1 auto;
                padding-left: 40px;
                @media (max-width:700px) {
                    padding-left: 0;
                    padding-bottom: 30px;
                }
            }
        }
    }
    &__info {
        margin-bottom: 100px;
        @media (max-width:500px) {
                margin-bottom: 15px;
            }
        @media (max-width:1000px) {
            flex-direction: column;
            br {
                display: none;
            }
        }
        &-left {
            min-width: 40%;
            font-family: Montserrat;
            font-style: normal;
            font-weight: 900;
            font-size: 54px;
            line-height: 66px;
            @media (max-width:1000px) {
                text-align: center;   
            }
            @media (max-width:800px) {
                font-size: 42px;
                line-height: 51px;
            }
            @media (max-width:600px) {
                font-size: 32px;
                line-height: 39px;
            }

            @media (max-width:400px) {
                font-size: 24px;
                line-height: 29px;
            }
        }
    }
    &__more {
        
        margin-bottom: 100px;
        @media (max-width:500px) {
                margin-bottom: 15px;
            }
        justify-content: space-between;
        @media (max-width:850px) {
            flex-wrap: wrap;   
        }
        @media (max-width:500px) {
                margin-bottom: 15px;
            }
        &-item {
            font-family: Rubik;
            font-style: normal;
            font-weight: 500;
            font-size: 20px;
            line-height: 24px;
            
            text-align: center;
            
            
            @media (max-width:600px) {
                font-size: 17px;
                line-height: 20px;
            }
            
            img {
                width: 100%;
            }
            span {
                display: block;
            }
        }
        & > a {
//            margin-right: 30px;
//            width: calc( 100% - 420px );
            width: 64.5%;
            @media (max-width:850px) {
                width: 100%;
                order: 1;
                margin-bottom: 25px;
            }
        }
        & > .flex {
            width: 32%;
            @media (max-width:850px) {
                width: 100%;
                order: 2;
                flex-direction: row!important;
                justify-content: space-between;
                & > a {
                    width: 48%;
                }
            }
            @media (max-width:535px) {
                flex-direction: column!important;
                & > a {
                    width: 100%;
                    margin-bottom: 25px;
                }
            }
            
        }
        &-img {
            border-radius: 10px;
            overflow: hidden; 
            display: block;
            position: relative;
            margin-bottom: 20px;
            img {
                width: 100%;
                display: block;
            }
            &:hover {
                &:before {
                    opacity: 1;   
                }
            }
            &:before {
                content: '';
                display: block;
                position: absolute;
                width: 86px;
                height: 86px;
                background: url(../images/news/play.png) no-repeat center $red;
                border: 10px solid #fff;
                border-radius: 50%;
                top: 50%;
                left: 50%;
                position: absolute;
                transform: translate(-50%,-50%);
                opacity: 0;
                transition: $tr;
            }
//            &:after {
//                content: '';
//                display: block;
//                position: absolute;
//                width: 186px;
//                height: 186px;
//                border: 2px solid rgba(255,255,255,0.66);
//                border-radius: 50%;
//                top: 50%;
//                left: 50%;
//                position: absolute;
//                transform: translate(-50%,-50%);
//            }
            
        }
        .flex {
            flex-direction: column;
            align-items: stretch;
            justify-content: space-between;
            
        }
    }
    &__support {
        justify-content: space-between;
        align-items: stretch;
        margin-bottom: 100px;
        flex-wrap: wrap;
        @media (max-width:500px) {
                margin-bottom: 15px;
            }
        &-item {
            padding: 40px 32px;
            width: 30%;
            @media (max-width:950px) {
                width: 100%;
                margin-bottom: 15px;
            }
            span {
                font-family: Montserrat;
                font-style: normal;
                font-weight: 900;
                font-size: 32px;
                line-height: 39px;
                
                color: $red;
                
                margin-right: 35px;
            }
        }
    }
    &__start {
        &-head {
            font-family: Montserrat;
            font-style: normal;
            font-weight: 900;
            font-size: 54px;
            line-height: 66px;
            
            margin-bottom: 40px;
            @media (max-width:1000px) {
                text-align: center;
            }
            @media (max-width:820px) {
                font-size: 42px;
                line-height: 51px;
            }
            @media (max-width:550px) {
                font-size: 32px;
                line-height: 39px;
            }
            @media (max-width:500px) {
                font-size: 24px;
                line-height: 29px;
            }
            
        }
        &-block {
            flex-wrap: wrap;
            justify-content: space-between;
            & > .btn {
                width: 30%;
                @media (max-width:769px) {
                    width: 100%;
                    margin-bottom: 15px;
                }
            }
        }
        &-item {
            width: 30%;
            margin-bottom: 30px;
            font-family: Rubik;
            font-style: normal;
            font-weight: 500;
            font-size: 20px;
            line-height: 24px;
            text-align: center;
            span {
                display: inline-block;
                &:not(.page__more-img) {
                    
                }
            }
        }
    }
    &__news {
        &-head {
            justify-content: flex-start;
            margin-bottom: 40px;
            @media (max-width:769px) {
                flex-wrap: wrap;
            }
            @media (max-width:500px) {
                margin-bottom: 15px;
            }
            
            .flex {
                justify-content: space-between;
                min-width: 312px;
                padding-right: 60px;
                
            }
            span {
                color: $red;
            }
            & * {
                font-family: Montserrat;
                font-style: normal;
                font-weight: 900;
                font-size: 32px;
                line-height: 150%;
            }
            @media (max-width:769px) {
                b {
                    min-width: 100%;
                }      
            }
            @media (max-width:600px) {
                & * {
                    font-size: 24px;  
                }
            }
            @media (max-width:400px) {
                & * {
                    font-size: 20px;  
                }
            }
        }
        &-img {
            border-radius: 10px;
            overflow: hidden;
            margin-bottom: 43px;
            img {
                width: 100%;
            }
        }
        &-social {
            min-width: 312px;
            @media (max-width:900px) {
                order: 2;   
                margin-bottom: 30px;
            }
            a {
                display: flex;
                justify-content: center;
                align-items: center;
                min-width: 48px;
                min-height: 48px;
                border: 2px solid $red;
                border-radius: 50%;
                margin-right: 20px;
            }
        }
        &-cont {
            max-width: 710px;
            @media (max-width:900px) {
                max-width: 100%;
            }
            font-family: Rubik;
            font-style: normal;
            font-weight: normal;
            font-size: 17px;
            line-height: 150%;
            
            
            margin-bottom: 50px;
            @media (max-width:900px) {
                margin-bottom: 20px;
            }
            & > p {
                &:first-child {
                    padding-top: 0;
                    margin-top: 0;
                }
                margin-bottom: 30px;
            }
            h3 {
                font-family: Montserrat;
                font-style: normal;
                font-weight: 900;
                font-size: 24px;
                line-height: 150%;
            }
            ul {
                
            }
            li {
                list-style: none;
                margin-bottom: 30px;
                position: relative;
                &:before {
                    content: '';
                    display: block;
                    position: absolute;
                    top: 12px;
                    left: -40px;
                    width: 30px;
                    height: 2px;
                    background: $red;
                }
            }
        }
        &-block {
            align-items: flex-start;
            @media (max-width:900px) {
                flex-wrap: wrap;
            }
        }
    }
    &__video {
        margin-bottom: 100px;
        
        @media (max-width:535px) {
            margin-bottom: 0;   
        }
        .page__more {
            margin-bottom: 20px;
            @media (max-width:535px) {
                margin-bottom: 0;   
            }
        }
    }
    &_news {
        @media (max-width:800px) {
            .page__start-item {
                width: 48%;
            } 
        }
        @media (max-width:600px) {
            .page__start-item {
                width: 100%;
            } 
        }
        &-js {
            .slick-list {
                margin: 0px;
            }
            .slick-dots {
                padding-top: 10px;
            }
        }
    }
}

.pager {
    justify-content: center;
    border-top: 1px solid #F9F9F9;
    a {
        width: 60px;
        height: 60px;
        min-width: 60px;
        min-height: 60px;
        
        margin: 40px 15px;
        border-radius: 50%;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        
        font-family: Rubik;
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;

        &.active {
            background: $red;
            color: #fff;
        }
        @media (max-width:500px) {
            margin: 19px 1px;
        }
    }
}

.dis {
    justify-content: space-between;
    flex-wrap: wrap;
    position: relative;
    margin-bottom: 100px;
    @media (max-width:500px) {
                margin-bottom: 35px;
            }
    &-h2 {
        font-family: Montserrat;
        font-style: normal;
        font-weight: 900;
        font-size: 32px;
        line-height: 39px;
        margin-bottom: 35px;
        @media (max-width:500px) {
                margin-bottom: 15px;
            }
    }
    &-block {
        font-family: Rubik;
        font-style: normal;
        font-weight: normal;
        font-size: 17px;
        line-height: 150%;
        display: none;
        &.active {
            display: block;
        }
        & > .flex {
            margin-bottom: 13px;
            @media (max-width: 950px) {
                flex-wrap: wrap;
            }
            div:not(.tab) {
                min-width: 40%;
                position: relative;
                display: flex;
                align-items: center;
                cursor: pointer;
                @media (max-width:500px) {
                        width: 60%;
    
                }
                &.active {
                    color: $red;
                    @media (max-width: 950px) {
                        & ~ .tab {
                            display: block;
                            width: 100%;
                            border: 1px solid #000000;
                            border-radius: 10px;
                            padding: 0 10px;
                            margin-top: 15px;
                            margin-bottom: 15px;
                            position: relative;
                            &:before {
                                content: '';
                                position: absolute;
                                width: 20px;
                                height: 20px;
                                left: 26px;
                                background: #fff;
                                border-right: 1px solid #000;
                                border-bottom: 1px solid #000;
                                transform: rotate(225deg);
                                top: -11px;
                            }
                        }
                    }
                }
                &:before {
                    content: '';
                    display: inline-block;
                    width: 22px;
                    height: 22px;
                    background: url(../images/i.png) no-repeat center;
                    margin-right: 8px;
                    
                }
            }
            .tab {
                display: none;
            }
        }
        &-a {
            @media (max-width:500px) {
                & + span {
                    width: 40%;
                }  
            }
            
        }
    }
    &__left {
        width: 48%;
        border: 1px solid #000000;
        border-radius: 10px;
        padding: 40px;
        font-family: Rubik;
        font-style: normal;
        font-weight: normal;
        font-size: 17px;
        line-height: 150%;
        
        position: relative;
        display: none;
        @media (max-width:1100px) {
            width: 38%;
        }
        @media (max-width:950px) {
            display: none!important;
        }
        
        &.active {
            display: block;
        }
        h3 {
            font-family: Montserrat;
            font-style: normal;
            font-weight: 900;
            font-size: 24px;
            line-height: 150%;
        }
        & > span {
            position: absolute;
            width: 20px;
            height: 20px;
            right: -11px;
            background: #fff;
            border-right: 1px solid #000;
            border-bottom: 1px solid #000;
            transform: rotate(-45deg);
            top: 11px;
        }
        .tab {
            display: none;
            &.active {
                display: block;
            }
        }
    }
    &__right {
        width: 48%;
        padding-top: 10px;
        @media (max-width:1100px) {
            width: 58%;
        }
        @media (max-width: 950px) {
            width: 100%;   
        }
    } 
}

.more {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 15px;
    line-height: 18px;
    text-transform: uppercase;
    color: $red;
    position: absolute;
    bottom: -40px;
}

.best {
    justify-content: space-between;
    flex-wrap: wrap;
    &-item {
        width: 40%;
        align-items: center;
        justify-content: flex-start;
        
        margin-bottom: 47px;
        @media (max-width:1000px) {
            width: 48%; 
        }
        @media (max-width:765px) {
            width: 100%; 
        }
        
    }
    img {
        margin-right: 40px;
    }
    &-btn {
        @media (max-width:1000px) {
            display: flex;
            justify-content: center;
        }
    }
}

.form {
    padding: 7px;
    border: 1px solid #000;
    border-radius: 5px;
    margin-bottom: 100px;
    @media (max-width:650px) {
        border: none;
        flex-direction: column;
    }
    input {
        border: none;
        outline: none;
        flex: 1 1 100%;
        font-family: Rubik;
        font-style: normal;
        font-weight: normal;
        font-size: 24px;
        line-height: 150%;
        padding: 10px;
        @include plhl() {
            color: #CCCCCC;
        }
        @media (max-width:650px) {
            font-size: 17px;
            text-align: center;
            border: 1px solid #000;
            border-radius: 5px;
            margin-bottom: 15px;
        }
    }
}
