
.menu {
    margin: 0;
    padding: 0;
    li {
        list-style: none;
        & + li {
            margin-left: 40px;
        }
    }
    a {
        font-family: Montserrat;
        font-style: normal;
        font-weight: 600;
        font-size: 15px;
        line-height: 18px;
        text-transform: uppercase;
        transition: $tr;
        &:hover {
            color: $red;
        }
    }
    &-footer {
        li {
            & + li {
                margin-left: 0px;
            }
        }
        a {
            font-family: Rubik;
            font-style: normal;
            font-weight: normal;
            font-size: 17px;
            line-height: 20px;
            text-transform: none;
        }
    }
}
