
.footer {
    background: #000 ;
    padding: 111px 0 100px 0;
    @media (max-width:765px) {
        padding: 50px 0;   
    }
    border-bottom: 10px solid $red;
    color: #fff;
    
    font-family: Rubik;
    font-style: normal;
    font-weight: normal;
    font-size: 17px;
    line-height: 20px;
    overflow: hidden;
    
    a {
        color: #fff;
        transition: $tr;
        &:hover {
            color: $red;
        }
    }
    .logo {
        font-family: Rubik;
        font-style: normal;
        font-weight: normal;
        font-size: 24px;
        line-height: 28px;
        span {
            display: block;
            
        }
        @media (max-width:765px) {
            margin: 0 0 50px 0;
            display: block;
            img {
                margin: 0 0 30px 0;
                width: 100%;
            }   
        }
    }
    &-block {
        margin-right: 60px;
        @media (max-width:1100px) {
            flex:1 1 33%;
            margin-right: 0;
        }
        &-b {
            font-size: 13px;
            line-height: 15px;
            color: #999999;
            margin-top: -40px;
            @media (max-width:1080px) {
                margin-top: 25px;
                width: 100%;
            }
            @media (max-width:765px) {
                flex-direction: column;
                justify-content: space-between;
                text-align: center;
                & > * {
                    margin-bottom: 10px;
                }
                & > :first-child {
                    margin-bottom: 25px;
                }
            }
        }
        .btn {
            margin: 40px auto 0 auto;
            
//            display: block;
            @media (max-width:1100px) {
                margin:0 ;
            }
        }
        &-s {
            margin-right: 0;
            @media (max-width:1100px) {
                width: 100%;
                margin-right: 0;
                margin-top: 30px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                & > * {
                    flex: 1 1 33%;
                    @media (max-width:765px) {
                        margin-bottom: 25px;
                    }
                }
                .social {
                    justify-content: flex-start;
                    @media (max-width:765px) {
                        justify-content: center;
                    }
                    &-a {
                        margin-right: 15px;
                        @media (max-width:765px) {
                            margin-left: 15px;
                        }
                    }
                }
            }
            @media (max-width:765px) {
                flex-direction: column;
                justify-content: center;
                align-items: center;
                text-align: center;
            }
        }
    }
    .wrapper {
        flex-wrap: wrap;
        position: relative;
        @media (min-width:1100px) {
            &:after {
                content: '';
                display: block;
                width: 417px;
                height: 418px;
                right: -344px;
                top: -111px;
                position: absolute;
                background: #000 url(../images/bg/footer.png) no-repeat top right;
            } 
        }
        @media (max-width:765px) {
            flex-direction: column;
            justify-content: center;
            align-items: center;
            li {
                text-align: center;
                margin-bottom: 20px;
            }
        }
        
    }
}
